import React from "react"
import "./style.css"
import classnames from "classnames"

const Container = ({ children, id, title, subtitle, style, className, reverse, padded }) => (
  <div id={id} className={classnames("vid_container", className, reverse ? "reverse" : "", padded ? "padded" : "")}
       style={style}>
    <div className={"containerinner"}>
      {children}
    </div>
  </div>
)

export default Container
