import React from "react"
import "./style.css"
import classnames from "classnames"

const Button = ({text, href, id, children, className,type, tagName, download, disabled, onClick}) => {
    var TagName = tagName || "a";
    return <TagName onClick={onClick} id={id} type={type} className={classnames("vidbutton", className, disabled ? 'disabledbutton' : '')}
                    download={download} href={href}>{children}</TagName>
}

export default Button