import React from "react"
import "./style.css"
import Button from "../button/button"
import translate from "../../translate"
import classnames from "classnames"
import {OutboundLink} from "gatsby-plugin-google-gtag";

const CTA = ({className, cta, children}) => (
    <div className={classnames("mycta", className)}>
        <img src={cta.image}/>
        <div className={"myctacontent"}>
            <h2>{cta.title}
                <h3>{cta.description}</h3>

                <h3 className={"contactinfo"}>

                    <OutboundLink className="mt-8" href={"tel:" + require('./../../thebakers').telephone.replace(/ /g, "")}>☎ {require('./../../thebakers').telephone}</OutboundLink>

                <OutboundLink
                    href={"mailto:" + require('./../../thebakers').mail}>✉ {require('./../../thebakers').email}</OutboundLink></h3>
            </h2>

            {!children && <Button href="/kontakt/">{cta.contact}</Button>}
            {children && children}

        </div>
    </div>
)

export default CTA
